import axios from 'axios'
import store from './store'
import router from './router'

// axios
axios.defaults.timeout = 5000;
axios.defaults.headers.post['Content-Type'] = 'application/json;charset=UTF-8';

// axios
const instance = axios.create();
instance.defaults.headers.post['Content-Type'] = 'application/json;charset=UTF-8';

axios.interceptors.request.use = instance.interceptors.request.use;


//request
instance.interceptors.request.use(
    config => {
        //vuextoken,
        if(store.state.token){
            config.headers.Authorization = `${store.state.token}`;
        }
        return config;
    },
    err => {
        return Promise.reject(err);
    }
);

//respone
instance.interceptors.response.use(
    response => {
        return response;
    },
    error => { //2XX
        if(error.response){
            switch(error.response.status){
                case 401:
                    store.dispatch('UserLoginout'); //token
                    router.replace({ //
                        path: 'login',
                        query: { redirect: router.currentRoute.fullPath } // path
                    });
            }
        }
        return Promise.reject(error.response);
    }
);

class User {

    static home(){
        return instance.get('/api/init')
    }
    
    static register(data){
        return instance.post('/api/register', data)
    }

    static login(data){
        return instance.post('/api/login', data)
    }

    static find_user(data){
        return instance.post('/api/find_user',data)
    }

    static login_out(token){
        return instance.get('/api/login_out',{
            params: {
                token:token
            }
        })
    }
}


export default User


